<template>
	<view class="nav">
		<view class="nav-top">
			<view class="back" @click="navback">
				返回 </view>
			<input class="sousuo" placeholder="输入商品名称" v-model="sousuocontent" confirm-type="search"
				@confirm="getsousuo">
			<text class="iconfont icon-sousuo"></text>
			<view class="backcontent" @click="sousuoback">取消</view>
		</view>
		<view class="nav-background">
		</view>
		<view class="nav-content">
			<view class="nav-content-box" v-for="(item,index) in list" :key="index">
				<view class="nav-img">
					<image class="img" :src="item.smallImg"></image>
				</view>
				<view class="name">{{item.name}}</view>
				<view class="enname">{{item.enname}}</view>
				<view class="price">￥{{item.price}}</view>
			</view>
		</view>
	</view>
	</view>
</template>

<script>
	import iconfont from "../../static/style/iconfont.css"
	export default {
		data() {
			return {
				sousuocontent: '',
				list: ''
			}
		},
		onLoad(option) {
			this.sousuocontent = option.name
			this.getshopping()

		},
		methods: {
			//搜索商品
			async getshopping() {
				let res = await uni.request({
					url: 'http://www.kangliuyong.com:10002/search',
					data: {
						appkey: 'U2FsdGVkX19WSQ59Cg+Fj9jNZPxRC5y0xB1iV06BeNA=',
						name: this.sousuocontent
					}
				})
				console.log(res)
				this.list = res[1].data.result
				console.log(this.list)
			},
			//搜索商品
			getsousuo() {
				this.getshopping()
			},
			//返回页面
			navback() {
				uni.navigateBack({
					delta: 1
				});
			},
			//清空搜索内容
			sousuoback() {
				this.sousuocontent = ''
			}

		}

	}
</script>

<style lang="less" scoped>
	.nav {
		.nav-top {

			margin: 30rpx;
			display: flex;
			position: relative;
			align-items: center;

			.back {
				color: #0c34ba;
			}

			.icon-sousuo {
				position: absolute;
				color: #0c34ba;
				left: 190rpx;
				top: 18rpx;
			}

			.sousuo {
				color: #c8c9cc;
				width: 400rpx;
				height: 60rpx;
				text-align: center;
				border-radius: 30rpx;
				background-color: #f7f8fa;
				font-size: 20rpx;
				margin-left: 100rpx;
			}
		}

		.nav-background {
			width: 100%;
			height: 200rpx;
			background-color: #0c34ba;
		}

		.nav-content {
			display: flex;
			position: relative;
			margin: 30rpx;
			top: -80rpx;
			border-radius: 30rpx;
			flex-wrap: wrap;
			background-color: #FFFFFF;

			.nav-content-box {
				width: 200rpx;
				margin: 15rpx;

				.nav-img {
					.img {
						width: 200rpx;
						height: 200rpx;
					}
				}

				.name {}

				.enname {
					margin-top: 10rpx;
					white-space: nowrap;
					overflow: hidden;
					text-overflow: ellipsis;
				}

				.price {
					color: #0c34ba;
					font-weight: bold;
					margin-top: 20rpx;
				}
			}
		}
	}
</style>
